import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';

/**
 * 채팅상담 활성화/비활성화 상태 관리 서비스
 * 글로벌 네비게이터에서 채팅상담 기능을 토글할 수 있게 함
 */
@Injectable({
  providedIn: 'root'
})
export class ChatConsultationToggleService {
  // 기본값: 비활성화 (false)
  private readonly STORAGE_KEY = 'chatConsultationEnabled';
  private enabledSubject: BehaviorSubject<boolean>;
  
  constructor() {
    // localStorage에서 이전 상태 복원 (없으면 false)
    const savedState = localStorage.getItem(this.STORAGE_KEY);
    const initialState = savedState !== null ? savedState === 'true' : false;
    
    this.enabledSubject = new BehaviorSubject<boolean>(initialState);
  }

  /**
   * 채팅상담 활성화 상태 Observable
   */
  get enabled$(): Observable<boolean> {
    return this.enabledSubject.asObservable();
  }

  /**
   * 현재 채팅상담 활성화 상태
   */
  isEnabled(): boolean {
    return this.enabledSubject.value;
  }

  /**
   * 채팅상담 활성화/비활성화 설정
   */
  setEnabled(enabled: boolean): void {
    this.enabledSubject.next(enabled);
    localStorage.setItem(this.STORAGE_KEY, enabled.toString());
    
  }

  /**
   * 채팅상담 토글
   */
  toggle(): boolean {
    const newState = !this.isEnabled();
    this.setEnabled(newState);
    return newState;
  }
}
